@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
}

body {
	font-family: 'Rubik', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background-color: #282c34;
	color: white;
	max-width: 1200px;
}

a {
	color: lightgray;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
.App{padding:1em}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.MatchPage{display:grid;grid-template-columns:1fr 3fr;grid-column-gap:10px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .match-page-header{padding:20px}

.YearSelector ol{list-style-type:none;padding:20px}.YearSelector li{margin-bottom:15px;text-align:end}.YearSelector .year-selector-header{padding:10px}

.MatchDetails{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetails .match-date,.MatchDetails .match-venue,.MatchDetails .match-result{margin-top:10px}.MatchDetails .additional-details{text-align:right}.MatchDetails .additional-details>p{margin-bottom:20px}.won-card{background-color:#54e1a3}.lost-card{background-color:#e15454}

.TeamPage{display:grid;grid-template-rows:1fr 1fr 1fr;grid-row-gap:0px;grid-template-columns:1fr 1fr 1fr 1fr;grid-column-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:center}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1rem}.TeamPage .team-name{font-size:4 rem;margin-bottom:20px}.TeamPage .match-detail-section{grid-column:span 4}

.MatchInfo{padding:10px;margin-bottom:40px}.MatchInfo .match-result{margin-top:10px}.won-card{background-color:#54e1a3}.lost-card{background-color:#e15454}

.HomePage .app-name{font-size:2rem;margin:30px auto}.HomePage .team-grid{padding-top:30px;display:grid;grid-template-columns:1fr 1fr;grid-row-gap:10px;grid-column-gap:10px}

.TeamTile{height:80px;text-align:center;border:solid 1px white;padding:30px}

